.project-heading{
    text-align: center;
    padding: 4rem 0 2rem 0;
}
.project-container{
    max-width: 1140px;
    margin: auto;
    display: grid;
    grid-template-columns: repeat(3,1fr);
    grid-gap: 40px;
    padding-bottom: 5rem;
}
.project-card{
    background: #1a1919;
    padding: 1.2rem 1rem;
}

.project-card img{
    width: 100%;
}

.project-title{
    color: #fff;
    padding: 1rem;
}
.pro-details p{
    padding: bottom 1rem ;
    text-align: justify;
    font-size: 1.1rem;
}
.pro-btns{
    display: flex;
    padding: 0.5rem 0;
    font-size: 1rem;
    cursor: pointer;
    text-transform: uppercase;
    justify-content: space-between;
    /* background: rgb(248, 217, 15); */
    color: #222;
    /* border: 1px solid #fff; */
    
}
.pro-btns .btn{
    padding: 0.5rem 1rem;
}

@media screen and (max-width:740px) {
    .project-container{
        max-width: 90%;
        margin: auto;
        
        grid-template-columns: 1fr;
        
    }
}